import * as React from "react";
import { useState } from "react";
import { sendHttpRequest, HttpVerb, ContentType } from "../../../utils/httpRequest";
import api from "../../api";
import { _t } from "../../../utils/Translator";
import { MainPages } from "../main/Main";
import Record, { RecordItem } from "../record/Record";
import DocTemplate, { DocTemplateItem } from "../doc_template/DocTemplate";
import Error from "../error/Error";
import Progress from "../progress/Progress";

interface GenerateDocumentPageProps {
  docTemplates: DocTemplateItem[];
  selectedDocTemplate: DocTemplateItem;
  onDocTemplateSelected: (t: DocTemplateItem) => void;
  getConnectionToken: () => string;
  goToPage: (page: MainPages) => void;
}

const GenerateDocumentPage: React.FC<GenerateDocumentPageProps> = (props: GenerateDocumentPageProps) => {
  const { docTemplates, selectedDocTemplate, onDocTemplateSelected, getConnectionToken, goToPage } = props;
  const [error, setError] = useState<string>("");
  const [processing, setProcessing] = useState<boolean>(false);
  const [selectedRecord, setSelectedRecord] = useState<RecordItem>(null);

  const generateDocument = async (template: DocTemplateItem, record: RecordItem) => {
    let response = null;
    try {
      const generateDocumentRequest = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.generateDocument,
        ContentType.Json,
        getConnectionToken(),
        { template_id: template && template.id, record_id: record && record.id },
        true
      );
      response = JSON.parse(await generateDocumentRequest.promise);
    } catch (error) {
      setError(String(error));
    }
    return response;
  };

  const onGenerateDocument = async () => {
    setProcessing(true);
    const response = await generateDocument(selectedDocTemplate, selectedRecord);
    if (response.result.success) {
      Word.run(async (context) => {
        context.document.insertFileFromBase64(response.result.data.datas, "Replace", {
          importTheme: true,
          importStyles: true,
          importParagraphSpacing: true,
          importPageColor: true,
          importDifferentOddEvenPages: true,
        });
        await context.sync();
      });
      goToPage(MainPages.MainPage);
    } else {
      setError(response.result.error);
    }
    setProcessing(false);
  };

  const getCancelButton = () => {
    return (
      <button title="Return" type="button" className="btn btn-default" onClick={() => goToPage(MainPages.MainPage)}>
        <i className="fa fa-arrow-left text-primary" aria-hidden="true"></i>
      </button>
    );
  };

  const getGenerateDocumentButton = () => {
    return (
      <button title="Confirm" type="button" className="btn btn-primary" onClick={onGenerateDocument}>
        {_t("Confirm")}
      </button>
    );
  };

  return (
    <div>
      <div className="page-title">
        <i className="fa fa-pencil fa-fw text-primary"></i>&nbsp;{_t("Generate Document")}
      </div>
      <Error error={error} setError={setError} />
      <DocTemplate
        docTemplates={docTemplates}
        selectedDocTemplate={selectedDocTemplate}
        onDocTemplateSelected={onDocTemplateSelected}
      />
      {selectedDocTemplate && (
        <Record
          selectedRecord={selectedRecord}
          onRecordSelected={setSelectedRecord}
          selectedDocTemplate={selectedDocTemplate}
        />
      )}
      <div className="d-flex justify-content-between page-button-container">
        <span>{getCancelButton()}</span>
        {selectedDocTemplate && <span>{getGenerateDocumentButton()}</span>}
      </div>
      <Progress processing={processing} />
    </div>
  );
};

export default GenerateDocumentPage;
