import React, { FC } from "react";
import { _t } from "../../../utils/Translator";
import SearchSelect from "../searchselect/SearchSelect";
import { TagItem } from "../tags/tag";
import Tags from "../tags/tags";
import { DocTemplateItem } from "./DocTemplate";
import api from "../../api";

export interface SaveDocTemplateProps {
  docTemplates: DocTemplateItem[];
  selectedDocTemplate: DocTemplateItem;
  onDocTemplateSelected: (p: DocTemplateItem) => void;
  saveNew: boolean;
  setSaveNew: (n: boolean) => void;
  newDocTemplateName: string;
  setNewDocTemplateName: (n: string) => void;
}

const SaveDocTemplate: FC<SaveDocTemplateProps> = ({
  docTemplates,
  selectedDocTemplate,
  onDocTemplateSelected,
  saveNew,
  setSaveNew,
  newDocTemplateName,
  setNewDocTemplateName,
}) => {
  const getDocTemplateButton = () => {
    if (selectedDocTemplate) {
      const url = `${api.baseURL}/web#id=${selectedDocTemplate.id}&model=document_automation.template&view_type=form`;
      return (
        <a href={url} target="_blank" rel="noreferrer">
          <i className="fa fa-external-link" aria-hidden="true"></i>
        </a>
      );
    } else {
      return null;
    }
  };

  return (
    <div>
      <div className="page-section-title">
        {_t("Document Template")}&nbsp;{getDocTemplateButton()}
      </div>
      <div className="form-check">
        <input
          className="form-check-input"
          type="checkbox"
          checked={saveNew}
          onChange={(e) => setSaveNew(e.target.checked)}
        />
        <label className="form-check-label">{_t("New")}</label>
      </div>
      <div className="mt-2">
        {saveNew ? (
          <input
            className="name-input form-control"
            type="text"
            placeholder={_t("Describe document template")}
            value={newDocTemplateName}
            onChange={(e) => setNewDocTemplateName(e.target.value)}
          />
        ) : (
          <SearchSelect
            items={docTemplates}
            selectedItem={selectedDocTemplate}
            onItemSelected={onDocTemplateSelected}
            selectText={_t("Select document template")}
            searchText={_t("Search document template")}
          />
        )}
      </div>
    </div>
  );
};

export default SaveDocTemplate;
