import * as React from "react";
import Tag, { TagItem } from "./tag";
import TagSelect from "./TagSelect";
import { _t } from "../../../utils/Translator";

export interface TagsProps {
  tags: TagItem[];
  selectedTags: TagItem[];
  onTagSelected: (t: TagItem) => void;
  onTagDeselected: (t: TagItem) => void;
}

export default class Tags extends React.Component<TagsProps> {
  render() {
    let { tags, selectedTags, onTagSelected, onTagDeselected } = this.props;
    let tagElements = [];
    for (let tag of selectedTags) {
      tagElements.push(<Tag onTagClicked={onTagDeselected} tag={tag} key={tag.id}></Tag>);
    }
    let selectTags = tags.filter((item) => {
      return !selectedTags.includes(item);
    });
    return (
      <div className="mt-2">
        <TagSelect text={_t("Add tag")} onTagSelected={onTagSelected} tags={selectTags}></TagSelect>
        {tagElements}
      </div>
    );
  }
}
