import * as React from "react";
import { _t } from "../../../utils/Translator";
import { MainPages } from "../main/Main";
import { UserData } from "../header/Header";

interface MainPageProps {
  goToPage: (page: MainPages) => void;
  userData: UserData;
}

const MainPage: React.FC<MainPageProps> = (props: MainPageProps) => {
  const { goToPage, userData } = props;

  const getOpenDocumentButton = () => {
    return (
      <button
        type="button"
        className="text-start btn btn-default w-100"
        onClick={() => goToPage(MainPages.OpenDocumentPage)}
      >
        <i className="fa fa-cloud-download fa-fw text-primary"></i>&nbsp; {_t("Open Document")}
      </button>
    );
  };

  const getSaveDocumentButton = () => {
    return (
      <button
        type="button"
        className="text-start btn btn-default w-100"
        onClick={() => goToPage(MainPages.SaveDocumentPage)}
      >
        <i className="fa fa-cloud-upload fa-fw text-primary"></i>&nbsp; {_t("Save Document")}
      </button>
    );
  };

  const getGenerateDocumentButton = () => {
    return (
      <button
        type="button"
        className="text-start btn btn-default w-100"
        onClick={() => goToPage(MainPages.GenerateDocumentPage)}
      >
        <i className="fa fa-pencil fa-fw text-primary"></i>&nbsp; {_t("Generate Document")}
      </button>
    );
  };

  const getOpenTemplateButton = () => {
    return (
      <button
        type="button"
        className="text-start btn btn-default w-100"
        onClick={() => goToPage(MainPages.OpenTemplatePage)}
      >
        <i className="fa fa-cloud-download fa-fw text-primary"></i>&nbsp; {_t("Open Template")}
      </button>
    );
  };

  const getSaveTemplateButton = () => {
    return (
      <button
        type="button"
        className="text-start btn btn-default w-100"
        onClick={() => goToPage(MainPages.SaveTemplatePage)}
      >
        <i className="fa fa-cloud-upload fa-fw text-primary"></i>&nbsp; {_t("Save Template")}
      </button>
    );
  };

  const generateBasicAiTextButton = () => {
    return (
      <button
        type="button"
        className="text-start btn btn-default w-100"
        onClick={() => goToPage(MainPages.GenerateBasicAiTextPage)}
      >
        <i className="fa fa-magic fa-fw text-primary"></i>&nbsp; {_t("Generate Text")}
      </button>
    );
  };

  const generateAdvancedAiTextButton = () => {
    return (
      <button
        type="button"
        className="text-start btn btn-default w-100"
        onClick={() => goToPage(MainPages.GenerateAdvancedAiTextPage)}
      >
        <i className="fa fa-magic fa-fw text-primary"></i>&nbsp; {_t("Generate Text")}
      </button>
    );
  };

  return (
    <div>
      <div className="page-title">
        <i className="fa fa-home fa-fw text-primary"></i>&nbsp;{_t("Home")}
      </div>
      <div className="card">
        <div className="card-body">
          <div className="page-section-title">{_t("Case Management")}</div>
          {getOpenDocumentButton()}
          {getSaveDocumentButton()}
        </div>
      </div>
      <div className="card">
        <div className="card-body">
          <div className="page-section-title">{_t("Document Automation")}</div>
          {getOpenTemplateButton()}
          {getSaveTemplateButton()}
        </div>
      </div>
      <div className="card">
        <div className="card-body">
          <div className="page-section-title">{_t("Artificial Intelligence")}</div>
          {userData?.ai_level === "basic" ? generateBasicAiTextButton() : generateAdvancedAiTextButton()}
        </div>
      </div>
    </div>
  );
};

export default MainPage;
