import * as React from "react";
import { HttpVerb, sendHttpRequest, ContentType } from "../../../utils/httpRequest";
import api from "../../api";
import { _t } from "../../../utils/Translator";

/* global Office localStorage window */

enum AuthenticationRequestError {
  None,
  InvalidScheme,
  DatabaseNotReachable,
  PermissionRefused,
  AuthenticationCodeExpired,
}

type loginProps = {
  onLogin: (token: string) => void;
};

type LoginState = {
  isCheckingUrl: boolean;
  isLoading: boolean;
  baseURL: string;
  authenticationRequestError: AuthenticationRequestError;
};

class Login extends React.Component<loginProps, LoginState> {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      baseURL: localStorage.getItem("baseURL") || "",
      authenticationRequestError: AuthenticationRequestError.None,
      isCheckingUrl: false,
    };
  }

  /**
   * Called when the user changes the base URL input.
   */
  onServerChange = (newValue?: string): void => {
    this.setState({
      baseURL: newValue,
      authenticationRequestError: AuthenticationRequestError.None,
      isCheckingUrl: false,
    });
  };

  /**
   * Ensure the URL starts with https:// and is a valid origin.
   */
  sanitizeDbUrl = (urlStr: string): string => {
    try {
      let tmpStr = urlStr;
      if (!tmpStr.startsWith("http://") && !tmpStr.startsWith("https://")) {
        tmpStr = "https://" + tmpStr;
      }
      const url = new URL(tmpStr);
      url.protocol = "https:";
      return url.origin;
    } catch {
      return null;
    }
  };

  /**
   * Start the login flow: sanitize URL, check server, open dialog, exchange token.
   */
  login = async () => {
    this.setState({ isCheckingUrl: true });

    const sanitizedURL = this.sanitizeDbUrl(this.state.baseURL);
    if (!sanitizedURL) {
      this.setState({
        isCheckingUrl: false,
        authenticationRequestError: AuthenticationRequestError.InvalidScheme,
      });
      return;
    }

    this.setState({ baseURL: sanitizedURL });
    api.baseURL = sanitizedURL;
    localStorage.setItem("baseURL", sanitizedURL);

    if (!(await this._isOdooDatabaseReachable())) {
      this.setState({
        isCheckingUrl: false,
        authenticationRequestError: AuthenticationRequestError.DatabaseNotReachable,
      });
      return;
    }

    const authCode = await this._openOdooLoginDialog();
    if (!authCode) {
      this.setState({
        isCheckingUrl: false,
        authenticationRequestError: AuthenticationRequestError.PermissionRefused,
      });
      return;
    }

    const accessToken = await this._exchangeAuthCodeForAccessToken(authCode);
    if (!accessToken) {
      this.setState({
        isCheckingUrl: false,
        authenticationRequestError: AuthenticationRequestError.AuthenticationCodeExpired,
      });
      return;
    }

    this.setState({
      isCheckingUrl: false,
      authenticationRequestError: AuthenticationRequestError.None,
    });

    // Store token in localStorage
    localStorage.setItem("odooConnectionToken", accessToken);

    // Notify parent component
    this.props.onLogin(accessToken);
  };

  _isOdooDatabaseReachable = async () => {
    const request = sendHttpRequest(HttpVerb.POST, api.baseURL + api.getAccessToken, ContentType.Json, null, {}, true);
    try {
      await request.promise;
      return true;
    } catch {
      return false;
    }
  };

  _openOdooLoginDialog = async () => {
    const options = {
      height: 65,
      width: 30,
      promptBeforeOpen: true,
    };

    const redirectToAddin = encodeURIComponent(api.addInBaseURL + "/odoo-login-redirect.html");
    const redirectToAuthPage = encodeURIComponent(
      api.authCodePage +
        "?scope=" +
        api.outlookScope +
        "&friendlyname=" +
        api.outlookFriendlyName +
        "&redirect=" +
        redirectToAddin
    );
    const loginURL = api.baseURL + api.loginPage + "?redirect=" + redirectToAuthPage;
    const url = `${api.addInBaseURL}/dialog.html?dialogredir=${loginURL}`;

    return new Promise((resolve, _) => {
      Office.context.ui.displayDialogAsync(url, options, (asyncResult) => {
        const dialog = asyncResult.value;
        dialog.addEventHandler(Office.EventType.DialogMessageReceived, (_arg) => {
          dialog.close();
          const searchParams = new URL(JSON.parse(_arg["message"]).value).searchParams;
          const success = searchParams.get("success");
          if (success === "1") {
            const authCode = searchParams.get("auth_code");
            resolve(authCode && authCode.length ? authCode : null);
          } else {
            resolve(null);
          }
        });
      });
    });
  };

  _exchangeAuthCodeForAccessToken = async (authCode) => {
    try {
      return sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getAccessToken,
        ContentType.Json,
        null,
        { auth_code: authCode },
        true
      ).promise.then((response) => {
        const parsed = JSON.parse(response);
        const accessToken = parsed.result.access_token;
        return accessToken && accessToken.length ? accessToken : null;
      });
    } catch {
      return null;
    }
  };

  _getErrorMessage = () => {
    const ERROR_MESSAGES = {
      [AuthenticationRequestError.InvalidScheme]:
        'The database URL is invalid. It should follow the scheme "https://mycompany.innoverius.cloud". ',
      [AuthenticationRequestError.DatabaseNotReachable]:
        "Could not connect to your database. Make sure the module is installed in Odoo. ",
      [AuthenticationRequestError.PermissionRefused]: "Permission to access your database needs to be granted. ",
      [AuthenticationRequestError.AuthenticationCodeExpired]: "Your authentication code is invalid or has expired. ",
    };

    const errorStr = ERROR_MESSAGES[this.state.authenticationRequestError];
    if (errorStr) {
      return (
        <div className="alert alert-danger mt-3 mb-3" role="alert">
          {errorStr}
        </div>
      );
    }
    return null;
  };

  _getLoginButton = () => {
    if (this.state.isCheckingUrl) {
      return (
        <div role="button" className="btn btn-primary w-100">
          {_t("Connecting")}&nbsp;<i className="fa fa-spinner fa-spin" aria-hidden="true"></i>
        </div>
      );
    }
    return (
      <div role="button" className="btn btn-primary w-100" onClick={this.login}>
        {_t("Connect")}
      </div>
    );
  };

  render() {
    const errorMessage = this._getErrorMessage();
    return (
      <div className="bg-light">
        <div className="login d-flex align-items-center justify-content-center vh-100">
          <div className="w-100 text-center">
            <i className="fa fa-sign-in fa-5x text-primary"></i>
            <h1 className="mb-5">{_t("Let's get started!")}</h1>
            <p>{_t("Please enter your website and connect.")}</p>
            <input
              className="form-control"
              type="text"
              placeholder="https://mycompany.innoverius.cloud"
              value={this.state.baseURL}
              onChange={(e) => this.onServerChange(e.target.value)}
            />
            <div className="mt-3">{this._getLoginButton()}</div>
            {errorMessage}
          </div>
        </div>
      </div>
    );
  }
}

export default Login;
