import * as React from "react";
import { sendHttpRequest, HttpVerb, ContentType } from "../../../utils/httpRequest";
import api from "../../api";
import { _t } from "../../../utils/Translator";
import { MainPages } from "../main/Main";
import { DocTemplateItem } from "../doc_template/DocTemplate";
import { extractDocument } from "../../../utils/wordUtils";
import SaveDocTemplate from "../doc_template/SaveDocTemplate";
import { useState } from "react";
import Error from "../error/Error";
import Progress from "../progress/Progress";

interface SaveDocTemplatePageProps {
  docTemplates: DocTemplateItem[];
  selectedDocTemplate: DocTemplateItem;
  onDocTemplateSelected: (t: DocTemplateItem) => void;
  onDocTemplateCreated: (t: DocTemplateItem) => void;
  getConnectionToken: () => string;
  goToPage: (page: MainPages) => void;
}

const SaveDocTemplatePage: React.FC<SaveDocTemplatePageProps> = (props: SaveDocTemplatePageProps) => {
  const { 
    docTemplates, 
    selectedDocTemplate, 
    onDocTemplateSelected, 
    onDocTemplateCreated,
    getConnectionToken, 
    goToPage 
  } = props;
  const [error, setError] = useState<string>("");
  const [processing, setProcessing] = useState<boolean>(false);
  const [saveNew, setSaveNew] = useState<boolean>(false);
  const [newDocTemplateName, setNewDocTemplateName] = useState<string>("");

  const saveDocTemplate = async (datas: string, template: DocTemplateItem) => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.saveDocTemplate,
        ContentType.Json,
        getConnectionToken(),
        { datas: datas, template_id: template && template.id },
        true
      );
      response = JSON.parse(await request.promise);
    } catch (error) {
      setError(String(error));
    }
    return response;
  };
  
  const saveNewDocTemplate = async (datas: string, docTemplateName: string) => {
    let response = null;
    try {
      let request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.saveNewDocTemplate,
        ContentType.Json,
        getConnectionToken(),
        { datas: datas, template_name: docTemplateName },
        true
      );
      response = JSON.parse(await request.promise);
    } catch (error) {
      setError(String(error));
    }
    return response;
  };


  const onSaveDocTemplate = async () => {
    setProcessing(true);
    const fileContent = await extractDocument();
    const datas = btoa(fileContent.toString());
    if (saveNew) {
        const response = await saveNewDocTemplate(datas, newDocTemplateName);
        if (response.result.success) {
          onDocTemplateCreated(response.result.data.template)
          onDocTemplateSelected(response.result.data.template)
            goToPage(MainPages.MainPage);
        } else {
          setError(response.result.error);
        }
    } else {
        const response = await saveDocTemplate(datas, selectedDocTemplate);
        if (response.result.success) {
          goToPage(MainPages.MainPage);
        } else {
          setError(response.result.error);
        }
    }
    setProcessing(false);
  };

  const getSaveDocTemplateButton = () => {
    return (
      <div role="button" className="btn btn-primary" onClick={onSaveDocTemplate}>
        <span>{_t("Confirm")}</span>
      </div>
    );
  };

  const getCancelButton = () => {
    return (
      <div title="Return" role="button" className="btn btn-default" onClick={() => goToPage(MainPages.MainPage)}>
        <i className="fa fa-arrow-left text-primary" aria-hidden="true"></i>
      </div>
    );
  };

  return (
    <div>
      <div className="page-title">
        <i className="fa fa-cloud-upload fa-fw text-primary"></i>&nbsp;{_t("Save Template")}
      </div>
      <Error error={error} setError={setError} />
      <SaveDocTemplate
        docTemplates={docTemplates}
        selectedDocTemplate={selectedDocTemplate}
        onDocTemplateSelected={onDocTemplateSelected}
        saveNew={saveNew}
        setSaveNew={setSaveNew}
        newDocTemplateName={newDocTemplateName}
        setNewDocTemplateName={setNewDocTemplateName}
      />
      <div className="d-flex justify-content-between page-button-container">
        <span>{getCancelButton()}</span>
        <span>{getSaveDocTemplateButton()}</span>
      </div>
      <Progress processing={processing} />
    </div>
  );
};

export default SaveDocTemplatePage;
