import * as React from "react";
import { sendHttpRequest, HttpVerb, ContentType } from "../../../utils/httpRequest";
import api from "../../api";
import { _t } from "../../../utils/Translator";
import Case, { CaseItem } from "../case/Case";
import { DocumentItem } from "../document/Document";
import { MainPages } from "../main/Main";
import { extractDocument } from "../../../utils/wordUtils";
import SaveDocument from "../document/SaveDocument";
import { useState } from "react";
import { TagItem } from "../tags/tag";
import Error from "../error/Error";
import Progress from "../progress/Progress";

interface SaveDocumentPageProps {
  selectedCase: CaseItem;
  onCaseSelected: (c: CaseItem) => void;
  documents: DocumentItem[];
  selectedDocument: DocumentItem;
  onDocumentSelected: (d: DocumentItem) => void;
  documentTags: TagItem[];
  onDocumentCreated: (d: DocumentItem) => void;
  getConnectionToken: () => string;
  goToPage: (page: MainPages) => void;
}

const SaveDocumentPage: React.FC<SaveDocumentPageProps> = (props: SaveDocumentPageProps) => {
  const {
    selectedCase,
    onCaseSelected,
    documents,
    selectedDocument,
    onDocumentSelected,
    documentTags,
    onDocumentCreated,
    getConnectionToken,
    goToPage,
  } = props;
  const [error, setError] = useState<string>("");
  const [processing, setProcessing] = useState<boolean>(false);
  const [saveNew, setSaveNew] = useState<boolean>(false);
  const [newDocumentName, setNewDocumentName] = useState<string>("");
  const [selectedDocumentTags, setSelectedDocumentTags] = useState<TagItem[]>([]);

  const saveDocument = async (datas: string, document: DocumentItem) => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.saveDocument,
        ContentType.Json,
        getConnectionToken(),
        { datas: datas, document_id: document && document.id },
        true
      );
      response = JSON.parse(await request.promise);
    } catch (error) {
      setError(String(error));
    }
    return response;
  };

  const saveNewDocument = async (datas: string, c: CaseItem, documentName: string, documentTags: TagItem[]) => {
    let response = null;
    try {
      let request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.saveNewDocument,
        ContentType.Json,
        getConnectionToken(),
        { datas: datas, case_id: c && c.id, document_name: documentName, tag_ids: documentTags.map((t) => t.id) },
        true
      );
      response = JSON.parse(await request.promise);
    } catch (error) {
      setError(String(error));
    }
    return response;
  };

  const onSaveDocument = async () => {
    setProcessing(true);
    const fileContent = await extractDocument();
    const datas = btoa(fileContent.toString());
    if (saveNew) {
      const response = await saveNewDocument(datas, selectedCase, newDocumentName, selectedDocumentTags);
      if (response.result.success) {
        onDocumentCreated(response.result.data.document);
        onDocumentSelected(response.result.data.document);
      } else {
        setError(response.result.error);
      }
    } else {
      const response = await saveDocument(datas, selectedDocument);
      if (response.result.success) {
        goToPage(MainPages.MainPage);
      } else {
        setError(response.result.error);
      }
    }
    setProcessing(false);
  };

  const onDocumentTagDeselected = (tag: TagItem) => {
    setSelectedDocumentTags(selectedDocumentTags.filter((item) => item !== tag));
  };

  const onDocumentTagSelected = (tag: TagItem) => {
    if (!selectedDocumentTags.includes(tag)) {
      setSelectedDocumentTags(selectedDocumentTags.concat([tag]));
    }
  };

  const getSaveDocumentButton = () => {
    return (
      <button title="Confirm" type="button" className="btn btn-primary" onClick={onSaveDocument}>
        <span>{_t("Confirm")}</span>
      </button>
    );
  };

  const getCancelButton = () => {
    return (
      <button title="Return" type="button" className="btn btn-default" onClick={() => goToPage(MainPages.MainPage)}>
        <i className="fa fa-arrow-left text-primary" aria-hidden="true"></i>
      </button>
    );
  };

  return (
    <div>
      <div className="page-title">
        <i className="fa fa-cloud-upload fa-fw text-primary"></i>&nbsp;{_t("Save Document")}
      </div>
      <Error error={error} setError={setError} />
      <Case selectedCase={selectedCase} onCaseSelected={onCaseSelected} />
      {selectedCase && (
        <SaveDocument
          documents={documents}
          selectedDocument={selectedDocument}
          onDocumentSelected={onDocumentSelected}
          saveNew={saveNew}
          setSaveNew={setSaveNew}
          newDocumentName={newDocumentName}
          setNewDocumentName={setNewDocumentName}
          documentTags={documentTags}
          selectedDocumentTags={selectedDocumentTags}
          onDocumentTagSelected={onDocumentTagSelected}
          onDocumentTagDeselected={onDocumentTagDeselected}
        />
      )}
      <div className="d-flex justify-content-between page-button-container">
        <span>{getCancelButton()}</span>
        {selectedCase && <span>{getSaveDocumentButton()}</span>}
      </div>
      <Progress processing={processing} />
    </div>
  );
};

export default SaveDocumentPage;
