import React, { FC, useState } from "react";
import api from "../../api";
import { _t } from "../../../utils/Translator";
import { ContentType, HttpVerb, sendHttpRequest } from "../../../utils/httpRequest";
import { DocTemplateItem } from "../doc_template/DocTemplate";
import LazySearchSelect from "../searchselect/LazySearchSelect";
import Error from "../error/Error";

export interface RecordItem {
  name: string;
  id: number;
}

export interface RecordProps {
  selectedRecord: RecordItem;
  onRecordSelected: (r: RecordItem) => void;
  selectedDocTemplate: DocTemplateItem;
}

/* global localStorage */
const getConnectionToken = () => {
  return "Bearer " + localStorage.getItem("odooConnectionToken");
};

const Record: FC<RecordProps> = ({ selectedRecord, onRecordSelected, selectedDocTemplate }) => {
  const [records, setRecords] = useState<RecordItem[]>([]);
  const [error, setError] = useState<string>("");

  const searchRecords = async (query: string) => {
    if (!selectedDocTemplate || !query || query.length < 2) return;

    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.searchRecords, // <-- e.g. "/word/search_records"
        ContentType.Json,
        getConnectionToken(),
        {
          template_id: selectedDocTemplate.id,
          query: query,
        },
        true
      );

      const response = JSON.parse(await request.promise);

      if (response.result.success) {
        setRecords(response.result.data.records);
      } else {
        setError(response.result.error);
      }
    } catch (err) {
      setError(String(err));
    }
  };

  return (
    <div className="record-outer-container">
      <Error error={error} setError={setError} />
      <div className="page-section-title">{_t("Record")}</div>
      <LazySearchSelect
        items={records}
        selectedItem={selectedRecord}
        onItemSelected={onRecordSelected}
        selectText={_t("Select record")}
        searchText={_t("Search record")}
        onSearch={searchRecords}
      />
    </div>
  );
};

export default Record;
