import * as React from "react";
import { useEffect, useState } from "react";
import { ContentType, HttpVerb, sendHttpRequest } from "../../../utils/httpRequest";
import api from "../../api";
import type { CaseItem } from "../case/Case";
import type { DocumentItem } from "../document/Document";
import GenerateDocumentPage from "../pages/GenerateDocumentPage";
import MainPage from "../pages/MainPage";
import OpenDocumentPage from "../pages/OpenDocumentPage";
import OpenTemplatePage from "../pages/OpenTemplatePage";
import SaveDocumentPage from "../pages/SaveDocumentPage";
import SaveTemplatePage from "../pages/SaveTemplatePage";
import { TagItem } from "../tags/tag";
import { DocTemplateItem } from "../doc_template/DocTemplate";
import { PromptTemplateItem } from "../prompt_template/PromptTemplate";
import GenerateBasicAiTextPage from "../pages/GenerateBasicAiTextPage";
import GenerateAdvancedAiTextPage from "../pages/GenerateAdvancedAiTextPage";
import Header, { UserData } from "../header/Header";
import Footer from "../footer/Footer";
import Error from "../error/Error";

/* global localStorage */

const getConnectionToken = () => "Bearer " + localStorage.getItem("odooConnectionToken");

export enum MainPages {
  MainPage,
  OpenDocumentPage,
  SaveDocumentPage,
  SaveTimesheetPage,
  GenerateDocumentPage,
  OpenTemplatePage,
  SaveTemplatePage,
  GenerateBasicAiTextPage,
  GenerateAdvancedAiTextPage,
}

type MainProps = { disconnect: () => void };

const Main: React.FC<MainProps> = ({ disconnect }): JSX.Element => {
  const [error, setError] = useState<string>("");
  const [page, setPage] = useState<MainPages>(MainPages.MainPage);

  const [selectedCase, setSelectedCase] = useState<CaseItem | null>(null);
  const [documentsFromSelectedCase, setDocumentsFromSelectedCase] = useState<DocumentItem[]>([]);
  const [selectedDocument, setSelectedDocument] = useState<DocumentItem | null>(null);

  const [documentTags, setDocumentTags] = useState<TagItem[]>([]);
  const [userData, setUserData] = useState<UserData | null>(null);

  const [docTemplates, setDocTemplates] = useState<DocTemplateItem[]>([]);
  const [selectedDocTemplate, setSelectedDocTemplate] = useState<DocTemplateItem | null>(null);

  const [promptTemplates, setPromptTemplates] = useState<PromptTemplateItem[]>([]);
  const [selectedPromptTemplate, setSelectedPromptTemplate] = useState<PromptTemplateItem | null>(null);

  /* ========================== Helpers ========================== */

  // Load initial app data (tags, templates, user)
  const getData = async () => {
    try {
      const req = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getData,
        ContentType.Json,
        getConnectionToken(),
        {},
        true
      );
      return JSON.parse(await req.promise);
    } catch (e) {
      setError(String(e));
      return null;
    }
  };

  // Load case data
  const getCaseDataById = async (caseId: number) => {
    try {
      const req = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getCaseData,
        ContentType.Json,
        getConnectionToken(),
        { case_id: caseId },
        true
      );
      return JSON.parse(await req.promise);
    } catch (e) {
      setError(String(e));
      return null;
    }
  };

  // Resolve latest opened document for this user
  const resolveRecent = async (): Promise<{ case_id: number; document_id: number } | null> => {
    try {
      const req = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.resolveOpenedDocument,
        ContentType.Json,
        getConnectionToken(),
        {},
        true
      );
      const resp = JSON.parse(await req.promise);
      const root = resp?.result ?? resp;

      if (!root?.success) {
        setError(root?.error && root.error !== 0 ? String(root.error) : "Resolve failed.");
        return null;
      }
      const data = root?.data;
      if (!data?.case_id || !data?.document_id) {
        return null;
      }
      return { case_id: Number(data.case_id), document_id: Number(data.document_id) };
    } catch (e) {
      setError(String(e));
      return null;
    }
  };

  // Resolve latest opened template for this user.
  const resolveRecentTemplate = async (): Promise<{ template_id: number } | null> => {
    try {
      const req = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.resolveOpenedTemplate,
        ContentType.Json,
        getConnectionToken(),
        {},
        true
      );
      const resp = JSON.parse(await req.promise);
      const root = resp?.result ?? resp;

      if (!root?.success) {
        setError(root?.error && root.error !== 0 ? String(root.error) : "Resolve template failed.");
        return null;
      }
      const data = root?.data;
      if (!data?.template_id) {
        return null;
      }
      return { template_id: Number(data.template_id) };
    } catch (e) {
      setError(String(e));
      return null;
    }
  };

  /* =========================== Effects ========================= */

  // Initial data
  useEffect(() => {
    getData().then((response) => {
      const root = response?.result ?? response;
      if (root?.data) {
        setDocumentTags(root.data.document_tags || []);
        setDocTemplates(root.data.doc_templates || []);
        setPromptTemplates(root.data.prompt_templates || []);
        setUserData(root.data.user);
        setPage(MainPages.MainPage);
      }
    });
  }, []);

  // Auto-resolve case and document
  useEffect(() => {
    const run = async () => {
      const ids = await resolveRecent();
      if (!ids) return; // nothing recent; user can select manually

      const resp = await getCaseDataById(ids.case_id);
      const root = resp?.result ?? resp;
      if (!root?.success || !root?.data) {
        setError(root?.error && root.error !== 0 ? String(root.error) : "Failed to load case.");
        return;
      }

      const data = root.data;
      const caseObjFromApi = (data.case as CaseItem | undefined) || undefined;
      const docs = (data.documents || []) as DocumentItem[];

      // Ensure a case object with a readable name
      let caseItem: CaseItem;
      if (caseObjFromApi) {
        caseItem = caseObjFromApi;
      } else {
        const caseName =
          (data.case_name as string) ||
          (data.name as string) ||
          "";
        caseItem = { id: ids.case_id, name: caseName } as CaseItem;
      }

      setSelectedCase(caseItem);
      setDocumentsFromSelectedCase(docs || []);

      // Choose the real document object if present
      const found = docs.find((d) => Number(d.id) === Number(ids.document_id));
      if (found) {
        setSelectedDocument(found);
      } else {
        const stub = { id: ids.document_id, caseId: ids.case_id } as DocumentItem;
        setSelectedDocument(stub);
      }
    };

    run();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // Auto-resolve template
  useEffect(() => {
    if (!docTemplates || docTemplates.length === 0) return;

    const run = async () => {
      const res = await resolveRecentTemplate();
      if (!res) return;

      const found = docTemplates.find((t) => Number(t.id) === Number(res.template_id));
      if (found) {
        setSelectedDocTemplate(found);
      } else {
        // Minimal stub so UI can proceed (optional)
        setSelectedDocTemplate({ id: res.template_id, name: "Template" } as DocTemplateItem);
      }
    };

    run();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [docTemplates]);

  // When a case is selected (manually or programmatically), load its documents
  useEffect(() => {
    if (!selectedCase?.id) return;
    getCaseDataById(selectedCase.id).then((resp) => {
      const root = resp?.result ?? resp;
      if (!root?.success) {
        setError(root?.error && root.error !== 0 ? String(root.error) : "Failed to load documents for case.");
        return;
      }
      setDocumentsFromSelectedCase(root.data?.documents || []);
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedCase?.id]);

  // Clear selected document if user switches to a different case
  useEffect(() => {
    if (!selectedDocument) return;
    if (!selectedCase || selectedCase.id !== (selectedDocument as any).caseId) {
      setSelectedDocument(null);
    }
  }, [selectedCase, selectedDocument]);

  /* =========================== Pages =========================== */

  const onDocumentCreated = (d: DocumentItem) => {
    setDocumentsFromSelectedCase((prev) => (prev || []).concat([d]));
  };
  
  const onDocTemplateCreated = (d: DocTemplateItem) => {
    setDocTemplates((prev) => (prev || []).concat([d]));
  };

  const mainPage = <MainPage goToPage={setPage} userData={userData as UserData} />;

  const openDocumentPage = (
    <OpenDocumentPage
      selectedCase={selectedCase as CaseItem}
      onCaseSelected={setSelectedCase}
      documents={documentsFromSelectedCase}
      selectedDocument={selectedDocument as DocumentItem}
      documentTags={documentTags}
      onDocumentSelected={setSelectedDocument}
      getConnectionToken={getConnectionToken}
      goToPage={setPage}
    />
  );

  const saveDocumentPage = (
    <SaveDocumentPage
      selectedCase={selectedCase as CaseItem}
      onCaseSelected={setSelectedCase}
      documents={documentsFromSelectedCase}
      selectedDocument={selectedDocument as DocumentItem}
      onDocumentSelected={setSelectedDocument}
      documentTags={documentTags}
      onDocumentCreated={onDocumentCreated}
      getConnectionToken={getConnectionToken}
      goToPage={setPage}
    />
  );

  const generateDocumentPage = (
    <GenerateDocumentPage
      docTemplates={docTemplates}
      selectedDocTemplate={selectedDocTemplate as DocTemplateItem}
      onDocTemplateSelected={setSelectedDocTemplate}
      getConnectionToken={getConnectionToken}
      goToPage={setPage}
    />
  );

  const openTemplatePage = (
    <OpenTemplatePage
      docTemplates={docTemplates}
      selectedDocTemplate={selectedDocTemplate as DocTemplateItem}
      onDocTemplateSelected={setSelectedDocTemplate}
      getConnectionToken={getConnectionToken}
      goToPage={setPage}
    />
  );

  const saveTemplatePage = (
    <SaveTemplatePage
      docTemplates={docTemplates}
      selectedDocTemplate={selectedDocTemplate as DocTemplateItem}
      onDocTemplateSelected={setSelectedDocTemplate}
      onDocTemplateCreated={onDocTemplateCreated}
      getConnectionToken={getConnectionToken}
      goToPage={setPage}
    />
  );

  const generateBasicAiTextPage = (
    <GenerateBasicAiTextPage getConnectionToken={getConnectionToken} goToPage={setPage} />
  );

  const generateAdvancedAiTextPage = (
    <GenerateAdvancedAiTextPage
      getConnectionToken={getConnectionToken}
      selectedCase={selectedCase as CaseItem}
      onCaseSelected={setSelectedCase}
      promptTemplates={promptTemplates}
      selectedPromptTemplate={selectedPromptTemplate as PromptTemplateItem}
      onPromptTemplateSelected={setSelectedPromptTemplate}
      goToPage={setPage}
    />
  );

  const wrapPage = (pageEl: React.ReactNode) => (
    <>
      <Header userData={userData as UserData} />
      <div className="page">
        <Error error={error} setError={setError} />
        {pageEl}
      </div>
      <Footer disconnect={disconnect} />
    </>
  );

  switch (page) {
    case MainPages.MainPage:
      return wrapPage(mainPage);
    case MainPages.OpenDocumentPage:
      return wrapPage(openDocumentPage);
    case MainPages.SaveDocumentPage:
      return wrapPage(saveDocumentPage);
    case MainPages.GenerateDocumentPage:
      return wrapPage(generateDocumentPage);
    case MainPages.OpenTemplatePage:
      return wrapPage(openTemplatePage);
    case MainPages.SaveTemplatePage:
      return wrapPage(saveTemplatePage);
    case MainPages.GenerateBasicAiTextPage:
      return wrapPage(generateBasicAiTextPage);
    case MainPages.GenerateAdvancedAiTextPage:
      return wrapPage(generateAdvancedAiTextPage);
    default:
      return wrapPage(mainPage);
  }
};

export default Main;
